import './Card.css';
import React from "react";
import {ReactComponent as TrashBin} from "../../../icons/trash-2.svg";


/**
 * –ø–ª–∞—à–∫–∞ —Å 1 –ø–æ—Å–µ—â–µ–Ω–∏–µ–º
 */
export default function Card({visit, onDelete}) {
    const {
        receiptDate,
        doctorName,
        clinicName,
        analyzes,
        diagnosis,
        treatment,
        addressClinic,
    } = visit;

    return (
        <div className='card'>
            <div className="card__value">
                <h4 className="value__title"> –î–∞—Ç–∞ –ø—Ä–∏—ë–º–∞:</h4>
                <p className="value__text">{receiptDate}</p>
                <h4 className="value__title"> –§–ò–û –≤—Ä–∞—á–∞:</h4>
                <p className="value__text">{doctorName}</p>
                <h4 className="value__title"> –ê–¥—Ä–µ—Å –∫–ª–∏–Ω–∏–∫–∏:</h4>
                <p className="value__text"> {addressClinic}</p>
                <h4 className="value__title"> –ù–∞–∏–º–µ–Ω–æ–≤–∞–Ω–∏–µ –∫–ª–∏–Ω–∏–∫–∏:</h4>
                <p className="value__text">{clinicName}</p>
                <h4 className="value__title"> –ê–Ω–∞–ª–∏–∑—ã:</h4>
                <p className="value__text">{analyzes}</p>
                <h4 className="value__title"> –î–∏–∞–≥–Ω–æ–∑:</h4>
                <p className="value__text">{diagnosis}</p>
                <h4 className="value__title"> –õ–µ—á–µ–Ω–∏–µ:</h4>
                <p className="value__text">{treatment}</p>
            </div>

            <div
                className='card__delete'
                onClick={(e) => {
                    e.stopPropagation();
                    onDelete(visit.id)
                }}
            >
                <TrashBin className={'delete__icon'}/>
            </div>
        </div>
    )
}

